,0,0)\",\r\n        tooltip: {\r\n          trigger: \"item\",\r\n          formatter: \"{b}: <br/>{c} ({d}%)\",\r\n        },\r\n        color: [\"#af89d6\", \"#4ac7f5\", \"#0089ff\", \"#f36f8a\", \"#f5c847\"],\r\n        legend: {\r\n          //图例组件，颜色和名字\r\n          x: \"70%\",\r\n          y: \"center\",\r\n          orient: \"vertical\",\r\n          itemGap: 12, //图例每项之间的间隔\r\n          itemWidth: 10,\r\n          itemHeight: 10,\r\n          icon: \"rect\",\r\n          data: [\"玉米\", \"当归\", \"苹果\", \"土豆\", \"红薯\"],\r\n          textStyle: {\r\n            color: [],\r\n            fontStyle: \"normal\",\r\n            fontFamily: \"微软雅黑\",\r\n            fontSize: 12,\r\n          },\r\n        },\r\n        series: [\r\n          {\r\n            name: \" 品种占比\",\r\n            type: \"pie\",\r\n            clockwise: false, //饼图的扇区是否是顺时针排布\r\n            minAngle: 20, //最小的扇区角度（0 ~ 360）\r\n            center: [\"35%\", \"50%\"], //饼图的中心（圆心）坐标\r\n            radius: [50, 75], //饼图的半径\r\n            avoidLabelOverlap: true, ////是否启用防止标签重叠\r\n            itemStyle: {\r\n              //图形样式\r\n              normal: {\r\n                borderColor: \"#1e2239\",\r\n                borderWidth: 2,\r\n              },\r\n            },\r\n            label: {\r\n              //标签的位置\r\n              normal: {\r\n                show: true,\r\n                position: \"inside\", //标签的位置\r\n                formatter: \"{d}%\",\r\n                textStyle: {\r\n                  color: \"#fff\",\r\n                },\r\n              },\r\n              emphasis: {\r\n                show: true,\r\n                textStyle: {\r\n                  fontWeight: \"bold\",\r\n                },\r\n              },\r\n            },\r\n            data: data,\r\n          },\r\n          {\r\n            name: \"\",\r\n            type: \"pie\",\r\n            clockwise: false,\r\n            silent: true,\r\n            minAngle: 20, //最小的扇区角度（0 ~ 360）\r\n            center: [\"35%\", \"50%\"], //饼图的中心（圆心）坐标\r\n            radius: [0, 40], //饼图的半径\r\n            itemStyle: {\r\n              //图形样式\r\n              normal: {\r\n                borderColor: \"#1e2239\",\r\n                borderWidth: 1.5,\r\n                opacity: 0.21,\r\n              },\r\n            },\r\n            label: {\r\n              //标签的位置\r\n              normal: {\r\n                show: false,\r\n              },\r\n            },\r\n            data: data,\r\n          },\r\n        ],\r\n      };\r\n\r\n      // 使用刚指定的配置项和数据显示图表。\r\n      myChart.setOption(option);\r\n      window.addEventListener(\"resize\", function () {\r\n        myChart.resize();\r\n      });\r\n    };\r\n    const echarts_2 = () => {\r\n      // 基于准备好的dom，初始化echarts实例\r\n      var myChart = echarts.init(document.getElementById(\"echarts_2\"));\r\n\r\n      const option = {\r\n        backgroundColor: \"rgba(0,0,0,0)\",\r\n        tooltip: {\r\n          trigger: \"item\",\r\n          formatter: \"{b}  <br/>{c}辆\",\r\n        },\r\n        legend: {\r\n          x: \"center\",\r\n          y: \"2%\",\r\n          data: [\"粮食作物\", \"油料作物\", \"蔬菜作物\", \"果类\", \"药用作物\"],\r\n          icon: \"circle\",\r\n          textStyle: {\r\n            color: \"#fff\",\r\n          },\r\n        },\r\n        calculable: true,\r\n        series: [\r\n          {\r\n            name: \"车型\",\r\n            type: \"pie\",\r\n            //起始角度，支持范围[0, 360]\r\n            startAngle: 0,\r\n            //饼图的半径，数组的第一项是内半径，第二项是外半径\r\n            radius: [41, 110],\r\n            //支持设置成百分比，设置成百分比时第一项是相对于容器宽度，第二项是相对于容器高度\r\n            center: [\"50%\", \"20%\"],\r\n            //是否展示成南丁格尔图，通过半径区分数据大小。可选择两种模式：\r\n            // 'radius' 面积展现数据的百分比，半径展现数据的大小。\r\n            //  'area' 所有扇区面积相同，仅通过半径展现数据大小\r\n            roseType: \"area\",\r\n            //是否启用防止标签重叠策略，默认开启，圆环图这个例子中需要强制所有标签放在中心位置，可以将该值设为 false。\r\n            avoidLabelOverlap: false,\r\n            label: {\r\n              normal: {\r\n                show: true,\r\n                formatter: \"{c}m2\",\r\n              },\r\n              emphasis: {\r\n                show: true,\r\n              },\r\n            },\r\n            labelLine: {\r\n              normal: {\r\n                show: true,\r\n                length2: 1,\r\n              },\r\n              emphasis: {\r\n                show: true,\r\n              },\r\n            },\r\n            data: [\r\n              {\r\n                value: 600,\r\n                name: \"粮食作物\",\r\n                itemStyle: {\r\n                  normal: {\r\n                    color: \"#f845f1\",\r\n                  },\r\n                },\r\n              },\r\n              {\r\n                value: 1100,\r\n                name: \"油料作物\",\r\n                itemStyle: {\r\n                  normal: {\r\n                    color: \"#ad46f3\",\r\n                  },\r\n                },\r\n              },\r\n              {\r\n                value: 1200,\r\n                name: \"蔬菜作物\",\r\n                itemStyle: {\r\n                  normal: {\r\n                    color: \"#5045f6\",\r\n                  },\r\n                },\r\n              },\r\n              {\r\n                value: 1300,\r\n                name: \"果类\",\r\n                itemStyle: {\r\n                  normal: {\r\n                    color: \"#4777f5\",\r\n                  },\r\n                },\r\n              },\r\n              {\r\n                value: 1400,\r\n                name: \"药用作物\",\r\n                itemStyle: {\r\n                  normal: {\r\n                    color: \"#44aff0\",\r\n                  },\r\n                },\r\n              },\r\n              {\r\n                value: 0,\r\n                name: \"\",\r\n                label: {\r\n                  show: false,\r\n                },\r\n                labelLine: {\r\n                  show: false,\r\n                },\r\n              },\r\n              {\r\n                value: 0,\r\n                name: \"\",\r\n                label: {\r\n                  show: false,\r\n                },\r\n                labelLine: {\r\n                  show: false,\r\n                },\r\n              },\r\n              {\r\n                value: 0,\r\n                name: \"\",\r\n                label: {\r\n                  show: false,\r\n                },\r\n                labelLine: {\r\n                  show: false,\r\n                },\r\n              },\r\n              {\r\n                value: 0,\r\n                name: \"\",\r\n                label: {\r\n                  show: false,\r\n                },\r\n                labelLine: {\r\n                  show: false,\r\n                },\r\n              },\r\n              {\r\n                value: 0,\r\n                name: \"\",\r\n                label: {\r\n                  show: false,\r\n                },\r\n                labelLine: {\r\n                  show: false,\r\n                },\r\n              },\r\n            ],\r\n          },\r\n        ],\r\n      };\r\n\r\n      // 使用刚指定的配置项和数据显示图表。\r\n      myChart.setOption(option);\r\n      window.addEventListener(\"resize\", function () {\r\n        myChart.resize();\r\n      });\r\n    };\r\n    const mapDiv = ref(null);\r\n    const map = () => {\r\n      // 基于准备好的dom，初始化echarts实例\r\n      var myChart = echarts.init(mapDiv.value);\r\n      var mapName = \"leshan\";\r\n      echarts.registerMap(mapName, leshanJson);\r\n      const option = {\r\n        tooltip: {\r\n          formatter: function (params) {\r\n            console.log(params);\r\n            return [params.name + \":\"].join(\"<br>\");\r\n          },\r\n        },\r\n        visualMap: {\r\n          min: 500,\r\n          max: 3000,\r\n          text: [\"High\", \"Low\"],\r\n          realtime: false,\r\n          calculable: true,\r\n          inRange: {\r\