  visible.value = false;\r\n        getData();\r\n      });\r\n    };\r\n    const EditUser = (id) => {\r\n      visible.value = true;\r\n      userId.value = id;\r\n    };\r\n    onMounted(() => {\r\n      initChart();\r\n    });\r\n\r\n    const initChart = () => {\r\n      var chartDom = document.getElementById(\"echarts1\");\r\n      var myChart = echarts.init(chartDom);\r\n      var option;\r\n      option = {\r\n        xAxis: {\r\n          type: \"category\",\r\n          data: [\r\n            \"0时\",\r\n            \"1时\",\r\n            \"2时\",\r\n            \"3时\",\r\n            \"4时\",\r\n            \"5时\",\r\n            \"6时\",\r\n            \"7时\",\r\n            \"8时\",\r\n            \"9时\",\r\n            \"10时\",\r\n            \"11时\",\r\n            \"12时\",\r\n            \"13时\",\r\n            \"14时\",\r\n            \"15时\",\r\n            \"16时\",\r\n            \"17时\",\r\n            \"18时\",\r\n            \"19时\",\r\n            \"20时\",\r\n            \"21时\",\r\n            \"22时\",\r\n            \"23时\",\r\n          ],\r\n        },\r\n        yAxis: {\r\n          type: \"value\",\r\n        },\r\n        series: [\r\n          {\r\n            data: [\r\n              8, 7, 6, 5, 5, 6, 7, 12, 13, 17, 18, 19, 20, 20, 20, 18, 17, 17,\r\n              13, 10, 10, 10, 10, 9,\r\n            ],\r\n            type: \"line\",\r\n            smooth: true,\r\n          },\r\n        ],\r\n      };\r\n      option && myChart.setOption(option);\r\n    };\r\n\r\n    return {\r\n      params,\r\n      data,\r\n      pagination,\r\n      visible,\r\n      edit,\r\n      onMounted,\r\n      handleSave,\r\n      waterPNG,\r\n      O2,\r\n      NH4,\r\n      PH,\r\n    };\r\n  },\r\n});\r\n</script>\r\n<style>\r\n#components-layout-demo-fixed-sider .logo {\r\n  height: 32px;\r\n  margin: 16px;\r\n}\r\n.site-layout .site-layout-background {\r\n  background: #fff;\r\n}\r\n\r\n[data-theme=\"dark\"] .site-layout .site-layout-background {\r\n  ba